export default {
	data() {
		return {
			type: '',
			form: {
				name: '',
				unit: '化学学院',
				stock: '3',
				number: '',
			},
			isReceiveLoading: false
		}
	},
	onLoad(e) {
		console.log(e)
		let str = ''
		if (e.type === '1') {
			str += '耗材用品'
		} else if (e.type === '2') {
			str += '废液废品'
		}
		uni.setNavigationBarTitle({
			title:`${str} ${e.item}`
		})
		this.$set(this.form, 'name', `${str} ${e.item}`)
		this.type = e.type
	},
	methods: {
		handleReceive() {
			if (this.form.number.length === 0) {
				this.$toast('请先输入数量')
				return
			}
			this.isReceiveLoading = true
			setTimeout(() => {
				this.isReceiveLoading = false
				this.$toast('领用成功')
			}, 2000)
		},
		handleRecycle() {
			this.isReceiveLoading = true
			setTimeout(() => {
				this.isReceiveLoading = false
				this.$toast('回收成功')
			}, 2000)
		},
	},
}
